import axios from 'axios';
import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';

axios.defaults.baseURL = 'https://goit-phonebook-api.herokuapp.com';

const token = {
  set(token) {
    axios.defaults.headers.common.Authorization = `Bearer ${token}`;
    // common - –Ω–∞ –≤—Å–µ –∑–∞–ø—Ä–æ—Å—ã.(–º–æ–∂–µ–º –ø–æ—Å—Ç–∞–≤–∏—Ç—å –Ω–∞ post, get –∑–∞–ø—Ä–æ—Å—ã)
  },
  unset() {
    axios.defaults.headers.common.Authorization = '';
  },
};

// –ï—Å–ª–∏ –± –¥–µ–ª–∞–ª–∏ —á–µ—Ä–µ–∑ —Ñ–µ—Ç—á: fetch('url',{
// –æ–ø—Ü–∏–∏
// method: post;
// header: {Authorization: `Bearer ${token}`}
// })

/*
 * POST @ /users/signup
 * body: { name, email, password }
 * –ü–æ—Å–ª–µ —É—Å–ø–µ—à–Ω–æ–π —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏ –¥–æ–±–∞–≤–ª—è–µ–º —Ç–æ–∫–µ–Ω –≤ HTTP-–∑–∞–≥–æ–ª–æ–≤–æ–∫
 */
const register = createAsyncThunk('auth/register', async credentials => {
  try {
    const { data } = await axios.post('/users/signup', credentials);
    token.set(data.token);
    return data;
  } catch (error) {
    throw new Error(error);
  }
});

/*
 * POST @ /users/login
 * body: { email, password }
 * –ü–æ—Å–ª–µ —É—Å–ø–µ—à–Ω–æ–≥–æ –ª–æ–≥–∏–Ω–∞ –¥–æ–±–∞–≤–ª—è–µ–º —Ç–æ–∫–µ–Ω –≤ HTTP-–∑–∞–≥–æ–ª–æ–≤–æ–∫
 */
const logIn = createAsyncThunk('auth/login', async credentials => {
  try {
    const { data } = await axios.post('/users/login', credentials);
    token.set(data.token);
    return data;
  } catch (error) {
    throw new Error(error);
  }
});

/*
 * POST @ /users/logout
 * headers: Authorization: Bearer token
 * –ü–æ—Å–ª–µ —É—Å–ø–µ—à–Ω–æ–≥–æ –ª–æ–≥–∞—É—Ç–∞, —É–¥–∞–ª—è–µ–º —Ç–æ–∫–µ–Ω –∏–∑ HTTP-–∑–∞–≥–æ–ª–æ–≤–∫–∞
 */
const logOut = createAsyncThunk('auth/logout', async () => {
  try {
    await axios.post('/users/logout');
    token.unset();
  } catch (error) {
    throw new Error(error);
  }
});

const fetchCurrentUser = createAsyncThunk(
  'auth/refresh',
  async (_, thunkAPI) => {
    console.log(thunkAPI.getState());
    const state = thunkAPI.getState();
    const persistedToken = state.auth.token; //—Ç–æ–∫–µ–Ω —Å –ø—Ä–æ—à–ª–æ–π —Å–µ—Å—Å–∏–∏
    console.log(token);

    if (!persistedToken) {
      // –µ—Å–ª–∏ —Ç–æ–∫–µ–Ω–∞ –Ω–µ—Ç, –≤—ã—Ö–æ–¥–∏–º –∏–∑ fetchCurrentUser –∏ –≤–æ–∑–≤—Ä–∞—â–∞–µ–º thunkAPI.rejectWithValue(); –∏–ª–∏ state
      return thunkAPI.rejectWithValue();
    }
    token.set(persistedToken);
    try {
      const { data } = await axios.get('/users/current');
      return data;
    } catch (error) {
      throw new Error(error);
    }
  },
);

const operations = {
  register,
  logOut,
  logIn,
  fetchCurrentUser,
};
export default operations;
